import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from "@angular/router";
import { RestApiService } from '../rest-api.service';
@Component({
  selector: 'app-details',
  templateUrl: './details.component.html',
  styleUrls: ['./details.component.css']
})
export class DetailsComponent implements OnInit {
  public paramVal;
  public detailsData=[];
  public errorData;
  constructor(private route: ActivatedRoute, private _restService:RestApiService, private router: Router) { 
    this.route.params.subscribe(params => {
      console.log(params);
      this.paramVal = params.id;
    }
  );
  }

  ngOnInit() {
    this._restService.getAllScheduleDetails(this.paramVal).subscribe(
      data => {
        this.detailsData = data
        console.log(this.detailsData);
      },
      error =>  this.errorData = <any>error);
  }

}
