import { Component, OnInit } from '@angular/core';
import { RestApiService } from '../rest-api.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-maindata',
  templateUrl: './maindata.component.html',
  styleUrls: ['./maindata.component.css']
})
export class MaindataComponent implements OnInit {

  public scheduleData= [];
  public errorData;
  constructor(private _restService:RestApiService, private router: Router) {

  }

  getAllSchedule() {
    this._restService.getAllScheduleService().subscribe(
      data => {
        this.scheduleData = data
        console.log(this.scheduleData);
      },
      error =>  this.errorData = <any>error);
  }

  showDetail(id){
    this.router.navigate(['details', id]);
  }

  ngOnInit() {
  }

}
